<template>
  <div class="document-filters">
    <div class="filters-container">
      <!-- Filtre Type de document -->
      <div class="filter-dropdown" :class="{ active: activeFilter === 'type' }">
        <button 
          class="filter-button" 
          @click="toggleFilter('type')"
          :class="{ 'has-selection': selectedType }"
        >
          <i class="fas fa-file-alt me-2"></i>
          <span>Type de document</span>
          <span v-if="selectedType" class="selected-badge">{{ getTypeLabel(selectedType) }}</span>
          <i class="fas fa-chevron-down ms-2"></i>
        </button>
        <div v-if="activeFilter === 'type'" class="dropdown-menu show">
          <div class="dropdown-header">
            <span>Sélectionner un type</span>
            <button v-if="selectedType" @click.stop="clearFilter('type')" class="clear-btn">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="dropdown-item" 
               v-for="type in documentTypes" 
               :key="type.value"
               @click="selectType(type.value)"
               :class="{ active: selectedType === type.value }"
          >
            <i :class="type.icon" class="me-2"></i>
            <span>{{ type.label }}</span>
            <i v-if="selectedType === type.value" class="fas fa-check ms-auto"></i>
          </div>
        </div>
      </div>

      <!-- Filtre Personne -->
      <div class="filter-dropdown" :class="{ active: activeFilter === 'person' }">
        <button 
          class="filter-button" 
          @click="toggleFilter('person')"
          :class="{ 'has-selection': selectedPerson }"
        >
          <i class="fas fa-user me-2"></i>
          <span>Personne</span>
          <span v-if="selectedPerson" class="selected-badge">{{ getPersonLabel(selectedPerson) }}</span>
          <i class="fas fa-chevron-down ms-2"></i>
        </button>
        <div v-if="activeFilter === 'person'" class="dropdown-menu show">
          <div class="dropdown-header">
            <span>Sélectionner une personne</span>
            <button v-if="selectedPerson" @click.stop="clearFilter('person')" class="clear-btn">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="search-box">
            <i class="fas fa-search"></i>
            <input 
              type="text" 
              v-model="personSearch" 
              placeholder="Rechercher une personne..."
              @click.stop
            />
          </div>
          <div class="dropdown-items-list">
            <div v-if="loadingPersons" class="loading-item">
              <i class="fas fa-spinner fa-spin"></i> Chargement...
            </div>
            <div v-else-if="filteredPersons.length === 0" class="empty-item">
              Aucune personne trouvée
            </div>
            <div v-else
                 class="dropdown-item" 
                 v-for="person in filteredPersons" 
                 :key="person.id"
                 @click="selectPerson(person.id)"
                 :class="{ active: selectedPerson === person.id }"
            >
              <div class="person-avatar">
                <i class="fas fa-user"></i>
              </div>
              <div class="person-info">
                <div class="person-name">{{ person.prenom }} {{ person.nom }}</div>
                <div class="person-email">{{ person.email }}</div>
              </div>
              <i v-if="selectedPerson === person.id" class="fas fa-check ms-auto"></i>
            </div>
          </div>
        </div>
      </div>

      <!-- Filtre Service -->
      <div class="filter-dropdown" :class="{ active: activeFilter === 'service' }">
        <button 
          class="filter-button" 
          @click="toggleFilter('service')"
          :class="{ 'has-selection': selectedService }"
        >
          <i class="fas fa-building me-2"></i>
          <span>Service</span>
          <span v-if="selectedService" class="selected-badge">{{ getServiceLabel(selectedService) }}</span>
          <i class="fas fa-chevron-down ms-2"></i>
        </button>
        <div v-if="activeFilter === 'service'" class="dropdown-menu show">
          <div class="dropdown-header">
            <span>Sélectionner un service</span>
            <button v-if="selectedService" @click.stop="clearFilter('service')" class="clear-btn">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="dropdown-items-list">
            <div v-if="loadingServices" class="loading-item">
              <i class="fas fa-spinner fa-spin"></i> Chargement...
            </div>
            <div v-else-if="services.length === 0" class="empty-item">
              Aucun service trouvé
            </div>
            <div v-else
                 class="dropdown-item" 
                 v-for="service in services" 
                 :key="service.id"
                 @click="selectService(service.id)"
                 :class="{ active: selectedService === service.id }"
            >
              <i class="fas fa-building me-2"></i>
              <span>{{ service.nom }}</span>
              <i v-if="selectedService === service.id" class="fas fa-check ms-auto"></i>
            </div>
          </div>
        </div>
      </div>

      <!-- Bouton Réinitialiser -->
      <button 
        v-if="hasActiveFilters" 
        class="reset-filters-btn"
        @click="resetAllFilters"
        title="Réinitialiser tous les filtres"
      >
        <i class="fas fa-redo-alt me-2"></i>
        Réinitialiser
      </button>
    </div>

    <!-- Message d'accès refusé pour service -->
    <div v-if="showServiceAccessDenied" class="access-denied-message">
      <i class="fas fa-exclamation-triangle me-2"></i>
      Vous n'appartenez pas à ce service, vous n'avez accès à aucun résultat.
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import apiClient from '@/services/api';

export default defineComponent({
  name: 'DocumentFilters',
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:modelValue', 'filter-change'],
  data() {
    return {
      activeFilter: null as string | null,
      selectedType: null as string | null,
      selectedPerson: null as number | null,
      selectedService: null as number | null,
      
      personSearch: '',
      persons: [] as any[],
      services: [] as any[],
      currentUserService: null as number | null,
      
      loadingPersons: false,
      loadingServices: false,
      showServiceAccessDenied: false,
      
      documentTypes: [
        { value: 'pdf', label: 'PDF', icon: 'fas fa-file-pdf text-danger' },
        { value: 'image', label: 'Images', icon: 'fas fa-file-image text-info' },
        { value: 'word', label: 'Word', icon: 'fas fa-file-word text-primary' },
        { value: 'excel', label: 'Excel', icon: 'fas fa-file-excel text-success' },
        { value: 'powerpoint', label: 'PowerPoint', icon: 'fas fa-file-powerpoint text-warning' },
        { value: 'archive', label: 'Archives', icon: 'fas fa-file-archive text-secondary' },
        { value: 'text', label: 'Texte', icon: 'fas fa-file-alt text-muted' },
        { value: 'other', label: 'Autres', icon: 'fas fa-file' }
      ]
    };
  },
  computed: {
    hasActiveFilters(): boolean {
      return !!(this.selectedType || this.selectedPerson || this.selectedService);
    },
    filteredPersons(): any[] {
      if (!this.personSearch) return this.persons;
      const search = this.personSearch.toLowerCase();
      return this.persons.filter(p => 
        `${p.prenom} ${p.nom}`.toLowerCase().includes(search) ||
        p.email.toLowerCase().includes(search)
      );
    },
    currentFilters(): any {
      return {
        type: this.selectedType,
        person: this.selectedPerson,
        service: this.selectedService
      };
    }
  },
  watch: {
    currentFilters: {
      deep: true,
      handler(newFilters) {
        this.$emit('update:modelValue', newFilters);
        this.$emit('filter-change', newFilters);
      }
    }
  },
  mounted() {
    this.fetchFilterOptions();
    
    // Fermer les dropdowns si on clique ailleurs
    document.addEventListener('click', this.handleClickOutside);
  },
  beforeUnmount() {
    document.removeEventListener('click', this.handleClickOutside);
  },
  methods: {
    toggleFilter(filterName: string) {
      if (this.activeFilter === filterName) {
        this.activeFilter = null;
      } else {
        this.activeFilter = filterName;
      }
    },
    handleClickOutside(event: MouseEvent) {
      const target = event.target as HTMLElement;
      if (!target.closest('.filter-dropdown')) {
        this.activeFilter = null;
      }
    },
    selectType(type: string) {
      this.selectedType = type;
      this.activeFilter = null;
      this.showServiceAccessDenied = false;
    },
    selectPerson(personId: number) {
      this.selectedPerson = personId;
      this.activeFilter = null;
      this.showServiceAccessDenied = false;
    },
    selectService(serviceId: number) {
      // Vérifier si l'utilisateur appartient à ce service
      if (this.currentUserService && serviceId !== this.currentUserService) {
        this.showServiceAccessDenied = true;
        this.selectedService = null;
        setTimeout(() => {
          this.showServiceAccessDenied = false;
        }, 5000);
      } else {
        this.selectedService = serviceId;
        this.showServiceAccessDenied = false;
      }
      this.activeFilter = null;
    },
    clearFilter(filterType: string) {
      if (filterType === 'type') {
        this.selectedType = null;
      } else if (filterType === 'person') {
        this.selectedPerson = null;
      } else if (filterType === 'service') {
        this.selectedService = null;
      }
      this.showServiceAccessDenied = false;
    },
    resetAllFilters() {
      this.selectedType = null;
      this.selectedPerson = null;
      this.selectedService = null;
      this.showServiceAccessDenied = false;
      this.activeFilter = null;
    },
    getTypeLabel(type: string): string {
      const typeObj = this.documentTypes.find(t => t.value === type);
      return typeObj ? typeObj.label : type;
    },
    getPersonLabel(personId: number): string {
      const person = this.persons.find(p => p.id === personId);
      return person ? `${person.prenom} ${person.nom}` : '';
    },
    getServiceLabel(serviceId: number): string {
      const service = this.services.find(s => s.id === serviceId);
      return service ? service.nom : '';
    },
    async fetchFilterOptions() {
      console.log('🔄 Chargement des options de filtres (appels directs)');
      
      // Charger les personnes
      this.loadingPersons = true;
      try {
        const response = await apiClient.get('/users');
        this.persons = response.data.data || response.data || [];
        console.log(`✅ ${this.persons.length} personne(s) chargée(s):`, this.persons);
      } catch (error) {
        console.error('❌ Erreur lors du chargement des personnes:', error);
        this.persons = [];
      } finally {
        this.loadingPersons = false;
      }
      
      // Charger les services
      this.loadingServices = true;
      try {
        const response = await apiClient.get('/services');
        this.services = response.data.data || response.data || [];
        console.log(`✅ ${this.services.length} service(s) chargé(s):`, this.services);
      } catch (error) {
        console.error('❌ Erreur lors du chargement des services:', error);
        this.services = [];
      } finally {
        this.loadingServices = false;
      }
      
      // Charger le service de l'utilisateur actuel depuis le store
      try {
        const user = this.$store.state.user;
        if (user && user.service_id) {
          this.currentUserService = user.service_id;
          console.log(`✅ Service utilisateur (depuis store): ${this.currentUserService}`);
        }
      } catch (error) {
        console.error('❌ Erreur lors de la récupération du service utilisateur:', error);
      }
    }
  }
});
</script>

